import React , {Component} from 'react';
import Navigation from './components/navigation/Navigation';
import Logo from './components/Logo/Logo';
import ImageSearch from './components/ImageSearch/ImageSearch';
import SignIn from './components/SignIn/SignIn';
import Register from './components/Register/Register';
import Rank from './components/Rank/Rank';
import './App.css';
import Particles from 'react-particles-js';
import Face from './components/Face/Face';

const para =
  {
    particles:{
      number: {
        value :80 ,        density : {
          enable : true ,
          value_area : 800
        }
      }} ,
      interactivity : {
    detect_on: "canvas",
    events: {
      onhover: {
        enable: true,
        mode: 'repulse'
      },
      },
    },
    }


const initialstate = {

input:'' ,
url :'', box:{},route:'SignIn',
user:{id:'',
     name:'',
     entries:''}
            
}


class App extends Component
{


constructor()
{
  super();
  this.state= initialstate;
}

calcFaceBox = (data) =>
{
  
  const cali =data;
  const im=document.getElementById('imagee');
  const width=Number(im.width);
  const height=Number(im.height);
  return{
  leftCol:cali.left_col*width,
  topRow : cali.top_row*height,
  rightCol:width-cali.right_col*width,
  bottomRow:height-(cali.bottom_row*height)
  
  };
  
  

}


onDisplayFace=(box)=>
{

 this.setState(()=>{return{box: box}});   // Important: read `state` instead of `this.state` when updating.

  
}
onValueChange = (event) =>
{
 this.setState({input:event.target.value});
}

onButtonClick=()=>
{
  
    this.setState({url:this.state.input});
     
      fetch('https://limitless-lake-82962.herokuapp.com/image',{
    method:'put',
    headers: {'Content-Type':'application/json'},
    body:JSON.stringify({
      id:this.state.user.id,
      url:this.state.input
  
    })
  }).then(response=>response.json()).then(data=>{this.setState({user:{name:data.name,entries:data.entries,id:data.id}});
    this.onDisplayFace(this.calcFaceBox(data));})


      

    .catch(err=>console.log("error"))

}

onRoute=(route)=>
{
this.setState({route:route});
if (route==='SignIn') {this.setState(initialstate);}
}


loadUser=(userid)=>
{
  this.setState({user:{id:userid.id,name:userid.name,entries:userid.entries}});
}


render() {

  return (
    <div className="App">
    <Particles className='particles' params={para}/>
    {(this.state.route==='SignIn')?
    <div className='pv6'>
    <SignIn onRouteChange={this.onRoute} load={this.loadUser}/></div> :((this.state.route==='Home')?
    (<div>
   <Navigation onRouteChange={this.onRoute}/>
   <Logo/>
 
   <Rank user={this.state.user}/>
   <ImageSearch onValue={this.onValueChange} onButton={this.onButtonClick}/>
   <Face box={this.state.box} image={this.state.url} />
   </div>):
    <div className='pv5'>
    <Register onRouteChange={this.onRoute}/>
    </div>)}
    </div>

  );
}
}
export default App;
