import React from 'react';
import Tilt from 'react-tilt';
import './Logo.css';
import brain from './brain.png';

const Logo = ()=>
{
	return (
		<div className="ma4 mt0">
		<Tilt className="Tilt br2 shadow-2" options={{ max : 55 }} style={{ height: 64, width: 64 }} >
 		<div className="Tilt-inner"> <img src ={brain} width='64px' height='64px' alt ='logo'/> </div>
		</Tilt>
		</div>
		);
}

export default Logo;