import React,{Component} from 'react';

class Register extends Component
{

  constructor(props)
  {
    super(props);
    this.state={
      name:'',
      email:'',
      password:'',
      confirmpassword:'',
      message:''
    }
  }

onNameChange =(event)=>
  {
this.setState({name:event.target.value});
  }
   onEmailChange =(event)=>
  {
this.setState({email:event.target.value});
  }
  onPasswordChange=(event)=>
  {
    this.setState({message:''});
this.setState({password:event.target.value});
  }
   onConPasswordChange=(event)=>
  {
    this.setState({message:''});
this.setState({confirmpassword:event.target.value});
  }

  onSubmit=()=>
{ 


  if(!((this.state.email).toLowerCase()).match('[a-z]*@[a-z]*.com'))
{
this.setState({message:"Invalid email format"});
return;
}

if ((this.state.name)==='') {

  this.setState({message:"Name should not be blank"});
  return;
}

  if((this.state.password).length<6)
  {

   this.setState({message:"Password should atleast 6 character long"}) ;
  }
  else
  {
  
  if(this.state.confirmpassword===this.state.password)
  {
   fetch('https://limitless-lake-82962.herokuapp.com/Register',{
    method:'post',
    headers: {'Content-Type':'application/json'},
    body:JSON.stringify({
      name:this.state.name,
      email:this.state.email,
      password:this.state.password
    })
  }).then(response=>response.json()).then(data=>{

    if(data.id)
  this.props.onRouteChange('SignIn');
else
this.setState({message:data});

})
}
else
{
this.setState({message:"Passwords not matching"});
}

}
}

  

render()
{

	
	return (
		<article className=" mt3 br3 ba pa3 dark-gray b--black-10 mv4 w-100 w-50-m w-25-l mw5 center shadow-5">
  	<div className="measure">
    <fieldset id="sign_up" className="ba b--transparent ph0 mh0">
      <legend className="f4 fw6 ph0 mh0">Register</legend>
      <div className="mt3">
        <label className="db fw6 lh-copy f6" htmlFor="name">Name</label>
        <input onChange={this.onNameChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="text" name="name"  id="name"/>
      </div>
      <div className="mt3">
        <label className="db fw6 lh-copy f6" htmlFor="email-address">Email</label>
        <input onChange={this.onEmailChange} className="pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="email" name="email-address"  id="email-address"/>
      </div>
      <div className="mv3">
        <label className="db fw6 lh-copy f6" htmlFor="password">Password</label>
        <input onChange={this.onPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="password"  id="password"/>
      </div>
       <div className="mv3">
        <label  className="db fw6 lh-copy f6" htmlFor="confirmpassword">Confirm password</label>
        <input onChange={this.onConPasswordChange} className="b pa2 input-reset ba bg-transparent hover-bg-black hover-white w-100" type="password" name="confirmpassword"  id="confirmpassword"/>
      </div>
    </fieldset>
    <div className="">
      <input onClick={this.onSubmit} className="b ph3 pv2 input-reset ba b--black bg-transparent grow pointer f6 dib" type="submit" value="Submit"/>
    </div>
    { (this.state.message!=='') ?
    <div className="lh-copy mt3">
      <p className="f6 link dim white db pointer">{this.state.message}</p>
    </div>:<div/>}
  </div>
  </article>
		);
}

}
export default Register;